<template>
  <div>
    <div class="slide-wrap" @mouseover="clearInv" @mouseout="runInv">
      <a :href="slides[nowIndex].href" class="slide-link">
        <transition  name="slide-trans">
          <img v-if="show":src="slides[nowIndex].src">
        </transition>
        <transition  name="slide-trans-old">
          <img v-if="!show":src="slides[nowIndex].src">
        </transition>
      </a>

      <div class="slide-bottom">
        <h3 class="slide-title">{{slides[nowIndex].title}}</h3>
        <ul>
          <li @click="goTo(prevIndex)"><</li>
          <li v-for="(item,index) in slides" @click="goTo(index)" :class="{'active': index==nowIndex}">{{index+1}}</li>
          <li @click="goTo(nextIndex)">></li>
        </ul>
      </div>
    </div>
  </div>

</template>
<script>
  export default {
    name: 'slideShow',
    data(){
      return {
        nowIndex: 0,
        show: true
      }
    },
    computed: {
      prevIndex: function(){
        if(this.nowIndex===0){
          return this.slides.length-1
        }else{
          return (this.nowIndex-1);
        }
      },
      nextIndex: function(){
        if(this.nowIndex===(this.slides.length-1)){
          return 0
        }else{
          return this.nowIndex+1;
        }
      },
    },
    props: ['slides','inv'],
    methods: {
      goTo(index){
        var me = this;
        this.show = false;
        setTimeout(function(){
          me.show = true;
          me.nowIndex= index
        },10)

      },
      runInv(){
        var me = this
        this.invId = setInterval(function(){
          me.goTo(me.nextIndex)
        },me.inv)
      },
      clearInv(){
        clearInterval(this.invId)
      }
    },
    mounted(){
      this.runInv();
    }
  }
</script>
<style>
  .slide-trans-enter-active {
    transition: all .5s;
  }
  .slide-trans-enter {
    transform: translateX(900px);
  }
  .slide-trans-old-leave-active {
    transition: all .5s;
    transform: translateX(-900px);
  }

  .slide-wrap {
    position: relative;
    padding: 15px 0 0 0;
    margin-bottom: 15px;
    overflow: hidden;
  }
  .slide-link {
    display: block;
    position: relative;
    width: 900px;
    height: 500px;
  }
  .slide-link img {
    position: absolute;
    top: 0;
    width: 100%;
  }
  .slide-bottom {
    position: absolute;
    bottom: 0;
    height: 50px;
    background: gray;
    color: white;
    width: 100%;
  }
  .slide-title {
    float: left;
    line-height:50px;
    padding-left: 20px;
    font-size: 24px;
  }
  .slide-bottom ul {
    float: right;
  }
  .slide-bottom ul li {
    float: left;
    padding: 0px 10px;
    line-height: 50px;
    font-size: 20px;
    cursor: pointer;
  }
  .slide-bottom ul li.active {
    color: green;
  }
</style>



